<template>
  <b-navbar type="dark" variant="info" toggleable="md">
    <b-navbar-toggle target="nav_dropdown_collapse"></b-navbar-toggle>
    <b-navbar-brand href="/">TestManager</b-navbar-brand>

    <b-collapse is-nav id="nav_dropdown_collapse">
      <b-navbar-nav class="ml-auto" v-if="checkAuthentication()">
        <b-nav-item @click="submitSignout()">Выйти</b-nav-item>
      </b-navbar-nav>
    </b-collapse>
  </b-navbar>
</template>

<script>
import Authentication from '@/components/pages/Authentication'
export default {
  methods: {
    submitSignout () {
      Authentication.signout(this, '/login')
    },

    checkAuthentication () {
      return Authentication.user.authenticated
    }
  }
}
</script>



