<template>
  <b-container>
    <app-header></app-header>

    <question-pack-table :fields="questionPackHeaders"></question-pack-table>
  </b-container>
</template>
<script>
import Header from '@/components/Header'
import QuestionPackTable from './../QuestionPackTable'

export default {
  components: {
    'app-header': Header,
    'question-pack-table': QuestionPackTable
  },

  data () {
    return {
      questionPackHeaders: [
        { key: 'title', label: 'Название' },
        { key: 'actions', label: 'Действия' }
      ]
    }
  },

  methods: {
  }
}
</script>



